import axios from 'axios'
import _ from 'underscore'
import { domain } from '@/config/'
import qs from 'qs'

const utils = {
  /**
   * 异步请求
   * @param option {Object} [设置]
   */
  ajax(option) {
    let myInterceptor
    let accessToken = (utils.getCache({ key: 'loginInfo' }) || {}).access_token
    let defaultOption = {
      baseURL: domain,
      url: '',
      data: '',
      method: 'post',
      headers: {
        access_token: accessToken,
        'Content-Type': 'application/x-www-form-urlencoded'
      },
      success: (res) => {
        console.log(res)
      },
      error: (err) => {},
      validateStatus: (status) => {
        return status >= 200 && status < 300 && status !== 207 // 默认的
      }
    }
    let opt = _.extend(defaultOption, option)
    opt.data = qs.stringify(opt.data)
    let _successCallBack = (res) => {
      opt.success(res)
    }
    let _errorCallBack = (err) => {
      console.error(err.toString())
        if(err.response.status===403){
          localStorage.removeItem('weiboLoginInfo')
          localStorage.removeItem('loginInfo')
        }
      opt.error(err)
    }
    let instance = axios.create()
    instance(opt).then((res) => {
      _successCallBack(res.data)
      if (opt.loading) {
        instance.interceptors.request.eject(myInterceptor)
      }
    }).catch((err) => _errorCallBack(err))
  },
  /**
   * 压缩图片
   * @param src {String} [src]
   * @param quality {Number} [质量]
   * @param callback {Object} [回调]
   */
  compressImage(src, quality, callback) {
    let img = new Image()
    img.src = src
    img.onload = function() {
      let width = this.width
      let height = this.height
      let canvas = document.createElement('canvas')
      let ctx = canvas.getContext('2d')
      canvas.setAttribute('width', width)
      canvas.setAttribute('height', height)
      ctx.drawImage(this, 0, 0, width, height)
      let base64 = canvas.toDataURL('image/jpeg', quality)
      callback(base64)
    }
  },
  /**
   * 坐标解析
   * @param point {Object} [坐标]
   * @param callback {Object} [回调]
   */
  getInfoByCoordinate(point, callback) {
    window.getLocationInfoCallback = (res) => {
      callback(res)
    }
    if (document.getElementById('baidu') !== null) {
      document.body.removeChild(document.getElementById('baidu'))
    }
    let script = document.createElement('script')
    script.id = 'baidu'
    script.src = `http://api.map.baidu.com/geocoder/v2/?callback=getLocationInfoCallback&location=${point.lat},${point.lng}&output=json&pois=1&ak=${baiduMapAk}`
    document.body.appendChild(script)
  },
  /**
   * 设置缓存
   * @param key {String} [键名]
   * @param value {Object} [键值]
   * @param es {Number} [保存的时间（s）] //不传长期保存
   * @param isSessionStorage {Boolean} [是否存sessionStorage]
   */
  setCache(option) {
    let defaultOption = {
      key: '',
      value: '',
      es: null,
      isSessionStorage: false
    }
    let opt = _.extend(defaultOption, option)
    // 设置过期原则
    if (!opt.value) {
      if (opt.isSessionStorage) {
        sessionStorage.removeItem(opt.key)
      } else {
        localStorage.removeItem(opt.key)
      }
    } else {
      let e
      if (opt.es === null) {
        e = null
      } else {
        e = opt.es * 1000
      }
      let exp = new Date()
      if (opt.isSessionStorage) {
        sessionStorage.setItem(opt.key, JSON.stringify({ value: opt.value }))
      } else {
        localStorage.setItem(opt.key, JSON.stringify({ value: opt.value, expires: e === null ? e : exp.getTime() + e, createtime: exp.getTime() }))
      }
    }
  },
  getCache(option) {
    let defaultOption = {
      isSessionStorage: false,
      key: ''
    }
    let opt = _.extend(defaultOption, option)
    if (localStorage.getItem(opt.key) !== null && !opt.isSessionStorage) {
      let o = JSON.parse(localStorage.getItem(opt.key))
      if ((!o || o.expires < Date.now()) && o.expires !== null) {
        localStorage.removeItem(opt.key)
        return null
      } else {
        return o.value
      }
    } else if (sessionStorage.getItem(opt.key) !== null && opt.isSessionStorage) {
      return JSON.parse(sessionStorage.getItem(opt.key))
    } else {
      return null
    }
  },
  /**
   * 数组分类
   * @param key {String} [键名]
   * @param array {Object} [数组]
   */
  classifyArrayByKey(key, array) {
    let map = {}
    let result = []
    for (let i = 0; i < array.length; i++) {
      let tempList = array[i]
      if (!map[tempList[key]]) {
        let temp = {}
        temp[key] = tempList[key]
        temp.data = [tempList]
        result.push(temp)
        map[tempList[key]] = tempList
      } else {
        for (let j = 0; j < result.length; j++) {
          let tempList1 = result[j]
          if (tempList1[key] === tempList[key]) {
            tempList1.data.push(tempList)
            break
          }
        }
      }
    }
    return result
  },
  /**
   * 表单验证
   * @param key {Object} [验证数组]
   *[{
        title:"身份证",
        value:"44018119921218091X",
        rule:"id", //rule:/asdasd/g,直接写正则也行
        require:true,
        select:true//提示文案变 ’请选择‘
   }]
   */
  validate(arr) {
    let pass = true
    let message = ''
    const rules = {
      id: /^[1-9]\d{5}(18|19|([23]\d))\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{3}[0-9Xx]$/, //身份证
      name: /^[\u4e00-\u9fa5]{2,6}$/, //姓名
      account: /^([1-9]{1})(\d{14}|\d{18})$/, //银行卡
      money: /^\d*(\.\d{0,2})?$/
    }
    for (let i = 0; i < arr.length; i++) {
      if (arr[i].require) {
        if (arr[i].value === undefined || arr[i].value === null || arr[i].value.toString().trim() === '') {
          pass = false
          message = '请输入'
          if (arr[i].select) { message = '请选择' }
          message += arr[i].title
          break
        } else if (typeof arr[i].rule === 'object' && !arr[i].rule.test(arr[i].value)) {
          pass = false
          message = '请输入正确的' + arr[i].title
          break
        } else if (rules[arr[i].rule] !== undefined && !rules[arr[i].rule].test(arr[i].value) && (typeof arr[i].rule === 'string')) {
          pass = false
          message = '请输入正确的' + arr[i].title
          break
        }
      }
    }
    if (!pass) {
      alert(message)
    }
    return pass
  }
}

//格式化日期
//new Date().format('yyyy-MM-dd hh:mm:ss')
Date.prototype.format = function(fmt) {
  let o = {
    'M+': this.getMonth() + 1,
    'd+': this.getDate(),
    'h+': this.getHours(),
    'm+': this.getMinutes(),
    's+': this.getSeconds(),
    'q+': Math.floor((this.getMonth() + 3) / 3),
    'S': this.getMilliseconds()
  }
  if (/(y+)/.test(fmt)) {
    fmt = fmt.replace(RegExp.$1, (this.getFullYear() + '').substr(4 - RegExp.$1.length))
  }
  for (let k in o) {
    if (new RegExp('(' + k + ')').test(fmt)) {
      fmt = fmt.replace(RegExp.$1, (RegExp.$1.length === 1) ? (o[k]) : (('00' + o[k]).substr(('' + o[k]).length)))
    }
  }
  return fmt
}

export default utils



