import Vue from 'vue'
import Router from 'vue-router'
import index from '@/components/index'
import wordpress from '@/components/wordpress'
import write from '@/components/write'

Vue.use(Router)

export default new Router({
  routes: [
      {
      path: '/',
      name: 'index',
      component: index
    },
    {
      path: '/wordpress',
      name: 'wordpress',
      component: wordpress
    },
        {
      path: '/write',
      name: 'write',
      component: write
    }
  ]
})



