<template>
  <div>
    <section>
      <my-header></my-header>
      <el-main>
        <el-input v-model="title" placeholder="请输入内容"></el-input>
        <el-select v-model="currentCategory" placeholder="请选择">
          <el-option v-for="item in category" :key="item.catagoryId" :label="item.categoryName" :value="item.catagoryId">
          </el-option>
        </el-select>
        <div style="width: 980px;">
          <script id="editor" type="text/plain"></script>
        </div>
        <el-row>
          <el-button v-on:click="doSubmit">提交</el-button>
        </el-row>
      </el-main>
    </section>
  </div>
</template>
<script>
import '../assets/UE/ueditor.config.js'
import '../assets/UE/ueditor.all.js'
import '../assets/UE/lang/zh-cn/zh-cn.js'
import '../assets/UE/ueditor.parse.js'
import myHeader from './header.vue'
export default {
  name: 'UE',
  components: {
    'my-header': myHeader
  },
  data() {
    return {
      editor: null,
      title: '',
      category: [],
      currentCategory: ''
    }
  },
  props: {
    defaultMsg: {
      type: String
    },
    config: {
      type: Object
    }
  },
  mounted() {
    this.getArticleCategory()
    this.editor = UE.getEditor('editor', {}) // 初始化UE
  },
  methods: {
    getUEContent() { // 获取内容方法
      return this.editor.getContent()
    },
    getArticleCategory() {
      this.$utils.ajax({
        url: '/api/article/getCategoryList',
        success: res => {
          if (res.status === 1) {
            this.category = res.result
          }
        }
      })
    },
    doSubmit() {
      console.log(this.currentCategory)
      this.$utils.ajax({
        url: '/api/article/publicArticle',
        data: {
          title: this.title,
          content: this.getUEContent(),
          categoryId: this.currentCategory
        }
      })
    }
  },
  destroyed() {
    this.editor.destroy()
  }
}

</script>
<style lang="less" scoped>
.el-container {
  height: 100%;

  >* {
    width: 100%;
  }
}

.el-row {
  margin-top: 10px;
}

</style>



