<template>
  <div>
    <el-header>
      <img src="../assets/logo.jpg">
      <div class="user-info" v-if="userInfo">
        <el-dropdown size="mini" trigger="click" @command="handleCommand">
          <span class="el-dropdown-link">
            <div class="avatar"><span :style="`background-image:url(${userInfo.userAvatar})`"></span></div>
            <span> {{userInfo.userNickName}}</span>
          </span>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item command="logout">退出登录</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </div>
      <div class="login" v-else @click="openLoginDialog"><a>登录 </a></div>
    </el-header>
    <el-dialog class="login-dialog" :title="loginDialogTitle" :visible.sync="loginDialogVisible" width="30%" center>
      <el-tabs :value="currentTab" @tab-click="handleClick" tab-position="left">
        <el-tab-pane label="登录" name="login">
          <el-row class="login-form">
            <el-form :model="loginForm" status-icon :rules="loginFormRules" ref="loginForm" class="demo-ruleForm">
              <el-form-item prop="username">
                <el-input v-model="loginForm.username" placeholder="请输入账号"></el-input>
              </el-form-item>
              <el-form-item prop="password">
                <el-input type="password" v-model="loginForm.password" placeholder="请输入密码" @keyup.enter.native="doSubmit('loginForm')"></el-input>
              </el-form-item>
              <el-form-item>
                <el-button v-on:click="doSubmit('loginForm')">登录</el-button>
              </el-form-item>
            </el-form>
          </el-row>
        </el-tab-pane>
        <el-tab-pane label="注册" name="register">
          <el-row class="register-form">
            <el-form :model="registerForm" status-icon :rules="registerFormRules" ref="registerForm" class="demo-ruleForm">
              <el-form-item prop="username">
                <el-input v-model="registerForm.username" placeholder="请输入账号"></el-input>
              </el-form-item>
              <el-form-item prop="password">
                <el-input type="password" v-model="registerForm.password" placeholder="请输入密码"></el-input>
              </el-form-item>
              <el-form-item prop="comfirmPassword">
                <el-input type="password" v-model="registerForm.comfirmPassword" placeholder="请输入确认密码"></el-input>
              </el-form-item>
              <el-form-item>
                <el-button v-on:click="doSubmit('registerForm')">注册</el-button>
              </el-form-item>
            </el-form>
          </el-row>
        </el-tab-pane>
      </el-tabs>
      <el-row class="third-party-login">
        <h1>第三方登录</h1>
        <div>
          <a v-on:click="go2LoginByWeibo" class="sina-login-btn"></a>
        </div>
      </el-row>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data() {
    return {
      userInfo: {},
      loginDialogVisible: false,
      loginDialogTitle: '登录',
      currentTab: 'login',
      password: '',
      username: '',
      loginForm: {
        username: '',
        password: ''
      },
      registerForm: {
        username: '',
        password: '',
        comfirmPassword: ''
      },
      loginFormRules: {
        username: [{
          validator: (rule, value, callback) => {
            if (!value) {
              return callback(new Error('账号不能为空'))
            }
            callback()
          },
          trigger: 'blur'
        }],
        password: [{
          validator: (rule, value, callback) => {
            if (!value) {
              return callback(new Error('密码不能为空'))
            }
            callback()
          },
          trigger: 'blur'
        }]
      },
      registerFormRules: {
        username: [{
          validator: (rule, value, callback) => {
            if (!value) {
              return callback(new Error('账号不能为空'))
            } else {
              this.$utils.ajax({
                url: '/api/users/getUserAvailable',
                data: {
                  username: value
                },
                success: res => {
                  if (res.status) {
                    callback()
                  } else {
                    return callback(new Error(res.result.message))
                  }
                }
              })
            }
          },
          trigger: 'blur'
        }],
        password: [{
          validator: (rule, value, callback) => {
            if (!value) {
              return callback(new Error('密码不能为空'))
            }
            callback()
          },
          trigger: 'blur'
        }],
        comfirmPassword: [{
          validator: (rule, value, callback) => {
            if (value === '') {
              callback(new Error('请再次输入密码'))
            } else if (value !== this.registerForm.password) {
              callback(new Error('两次输入密码不一致!'))
            } else {
              callback()
            }
          },
          trigger: 'blur'
        }]
      }
    }
  },
  methods: {
    handleCommand(command) {
      switch (command) {
        case 'logout':
          localStorage.removeItem('loginInfo')
          localStorage.removeItem('weiboLoginInfo')
          window.location.reload()
          break
      }
    },
    doSubmit(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          let isLogin = formName === 'loginForm'
          let url = '/api/users/login'
          if (!isLogin) {
            url = '/api/users/register'
          }
          this.$utils.ajax({
            url,
            data: {
              username: this[formName].username,
              password: this[formName].password
            },
            success: res => {
              if (isLogin) {
                if (res.status === 1) {
                  this.$utils.setCache({ key: 'loginInfo', value: res.result })
                  window.location.reload()
                } else {
                  this.$notify.error({
                    title: '错误',
                    message: res.result.message
                  })
                  this.$refs[formName].resetFields()
                }
              } else {
                if (res.status === 1) {
                  this.$notify({
                    title: '注册成功',
                    message: '恭喜你注册成功，马上去登录吧！',
                    type: 'success'
                  })
                  this.currentTab = 'login'
                  this.loginForm.username = this.registerForm.username
                  this.loginForm.password = this.registerForm.password
                  this.registerForm = {
                    username: '',
                    password: '',
                    comfirmPassword: ''
                  }
                }
              }
            }
          })
        }
      })
    },
    handleClick(tab, event) {
      this.loginDialogTitle = tab.label
      this.currentTab = tab.name
    },
    openLoginDialog() {
      this.loginDialogVisible = true
    },
    closeLoginDialog() {
      this.loginDialogVisible = false
    },
    go2LoginByWeibo() {
      window.location.href = 'https://api.weibo.com/oauth2/authorize?client_id=4197904860&response_type=code&redirect_uri=http://www.chenguohua.com.cn'
    },
    loginByWeibo() {
      let code = window.location.href.match(/\?code=(\S*)#\//)
      let weiboLoginInfo = this.$utils.getCache({ key: 'weiboLoginInfo' })
      let loginInfo = this.$utils.getCache({ key: 'loginInfo' })
      if (code !== null) {
        code = code[1]
        this.$utils.ajax({
          url: '/api/login/getWeiboAccessToken',
          data: {
            code
          },
          success: res => {
            this.$utils.setCache({ key: 'weiboLoginInfo', value: res })
            window.location.href = 'http://www.chenguohua.com.cn/#/'
          }
        })
      } else if (weiboLoginInfo && !loginInfo) {
        this.$utils.ajax({
          url: '/api/login/loginByWeibo',
          data: {
            access_token: weiboLoginInfo.access_token,
            uid: weiboLoginInfo.uid
          },
          success: res => {
            if (res.status) {
              console.log(res)
              this.userInfo = res.data
              this.$utils.setCache({ key: 'loginInfo', value: res.data })
            } else {
              window.location.reload()
            }
          }
        })
      } else {
        this.userInfo = loginInfo
      }
    }
  },
  mounted() {
    this.loginByWeibo()
  }
}

</script>
<style lang="less" scoped>
.el-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: @mainColor;

  img {
    width: 40px;
  }

  .login {
    font-size: 12px;
    color: @supportColor;

    a {
      cursor: pointer;
      text-decoration: underline;
    }
  }

  .user-info {
    .el-dropdown-link {
      display: flex;
      justify-content: space-between;
      align-items: center;
      cursor: pointer;

      >span {
        font-size: 12px;
        margin-left: 8px;
        color: #fff;
      }
    }

    .avatar {
      width: 36px;
      height: 36px;

      span {
        display: block;
        width: 36px;
        height: 36px;
        background-size: cover;
        background-position: center;
        border-radius: 50%;
        background-color: #fff;
      }
    }
  }
}

.login-dialog {

  .login-form,
  .register-form {
    button {
      width: 100%;
    }
  }

  .third-party-login {
    margin-top: 30px;

    h1 {
      margin-bottom: 10px;
    }

    div>a:hover {
      background-size: 120%;
    }

    .sina-login-btn {
      display: block;
      width: 30px;
      height: 30px;
      background-image: url(/static/images/sina.png);
      background-size: cover;
      border-radius: 50%;
      background-color: #fff;
      border: 2px solid @supportColor;
      cursor: pointer;
      background-position: center;
    }
  }
}

</style>



